        props: {
            hero: {
                type: String,
                Default: ""
            },
            attr: {
                type: String,
                Default: ""
            },
        },
        methods: {
            sendHero(){
                eventBus.$emit("hero-sent", this.hero, this.attr);
                console.log("hero Button Pressed!");
            },
            sendID(){
                this.$parent.sendID();
            }
        }
    }
