<template>
    
        <button v-if=scrolled v-on:click="scrollUp" class="scrollUp scrollButton">
            <i class="las la-angle-double-up"></i>
        </button>
        <button v-else v-on:click="scrollDown" class="scrollDown scrollButton">
            <i class="las la-angle-double-down"></i>
        </button>
        
</template>

<script>
    export default {
        name: 'ScrollButton',
        props: {
            scrolled: {
                type: Boolean,
                Default: false
            }
        },
        methods: {
            scrollUp() {
                this.$parent.scrollUp();
            },
            scrollDown() {
                this.$parent.scrollDown();
            }
        }
        
    }
</script>

<style>

.scrollButton {
    
    position: fixed;
    left: 15px;
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: solid 2px white;
    background-color: #3335;

}

.scrollButton i {
    font-size: 32px;
    color: white;
}

.scrollUp {
    top: 15px;
}
.scrollDown {
    bottom: 15px;
}

@media (min-width: 576px) {
    .scrollButton {
        display: none;
    }
}


</style>