<template>
    <div class="gameBoard">
        <Field v-bind:heroBlue=blue v-bind:heroRed=red v-bind:status=status />
        <Hand v-bind:hand=hand v-bind:status=status />
        <ScrollButton v-bind:scrolled=scrolledDown />
        <!-- <h1 v-if=playerBlue>Blue</h1>
        <h1 v-if=playerRed>Red</h1> -->
        <!-- testtestestestest -->
            
        
    </div>
    
</template>

<script>
    import io from "socket.io-client";
    import Hand from './Hand.vue';
    import Field from './Field.vue';
    import eventBus from './eventBus.js';
    import ScrollButton from './ScrollButton.vue';

    export default {
        name: 'GameBoard',
        components: {
            Hand,
            Field,
            ScrollButton
        },
        data() {
            return {
                socket: {},
                context: {},
                blue: [],
                red: [],
                hand: [],
                status: "",
                voted: false,
                id: 0,
                looser: 0,
                playerRed: false,
                playerBlue: false,
                scrolledDown: false
                
            }
        },
        created() {
            this.socket = io("https://ultrafight-server.herokuapp.com:3000");



            /* listener methods for socket communication */

            //hero-sent
            eventBus.$on("hero-sent", (hero, attr) => {
                console.log("hero successfully sent");
                console.log(hero);
                console.log(attr);
                this.socket.emit("sendHero", hero, attr);
                this.status = "wait";
            });
         },
        mounted() {
            document.addEventListener('scroll', () => {
                let scrollProportion = window.scrollY / (document.body.clientHeight - screen.height);
                console.log(scrollProportion);
               if (scrollProportion > 0.5) {
                   this.scrolledDown = true;
                   console.log(this.scrolledDown);
               } else {
                   this.scrolledDown = false;
                   console.log(this.scrolledDown);
               }
               
            });

            this.socket.emit("storeClientInfo");

            // Save ID
            this.socket.on("yourID", id => {
                this.id = id;
            });

            // Getting a new hand from server
            this.socket.on("newHand", hand => {
                this.hand = hand;
            });

            // Getting current heroes from server
            this.socket.on("currentPlayers", (playerBlue, playerRed) => {
                if (playerRed) {
                    this.red = playerRed;
                    console.log("Red: " + playerRed);
                }
                if (playerBlue) {
                    this.blue = playerBlue;
                    console.log("Blue: " + playerBlue);
                }
                console.log(playerRed);
                console.log(this.red);
                console.log(this.blue);
    
            });

            // new Blue Player

            this.socket.on("playerBlue", id => {
                if (id == this.id) {
                    this.status = "chooseHero";
                    this.playerBlue = true;
                } else {
                    //this.status = "wait";
                    this.playerBlue = false;
                }
            });

            this.socket.on("playerRed", id => {
                if (id == this.id) {
                    this.status = "chooseHero";
                    this.playerRed = true;
                } else {
                    this.playerRed = false;
                    //this.status = "wait";
                }
            });



            // Defining which side lost
            this.socket.on("newLooser", (looser, looserId, cards) => {
                this.status = "wait";
                console.log("Hero changed!");
                this.looser = looser;
                if (this.looser == 1) {
                    this.red = [];
                } else {
                    this.blue = [];
                }
                if (this.id == looserId) {
                    this.hand = cards;
                }
            });


            // Replacing hero
            this.socket.on("newHeroBlue", hero => {
                this.blue = hero;
            });

            this.socket.on("newHeroRed", hero => {
                this.red = hero; 
            });

            // Setting status for vote/fight phase
            // Server todo
            this.socket.on("yourFight", (id1, id2)=> {
                console.log("ID-Test: " + this.id);
                console.log(id2);
                if ((id1 == this.id) || (id2 == this.id)) {
                    this.status = "fight";
                    console.log("FIGHT!")
                } else {
                    this.status = "vote";
                    console.log("VOTE!")
                }
            });

            // Drawing random attribute for new hero
            this.socket.on("randomCard", card => {
                if (this.looser == 1) {
                    this.red[2] = card;
                } else {
                    this.blue[2] = card;
                }
            });

            // Setting player status, deprecated
            this.socket.on("newStatus", status => {
                switch(status) {
                    case "arguer":
                    // code block
                    break;
                    case "voter":
                    // code block
                    break;
                    default:
                    // code block
                } 
            })

            //Getting ID
            this.socket.on("yourID", id => {
                console.log(id);
            });

            



        },
        methods: {
            move(direction) { this.socket.emit("move", direction); },
            playCard() {
                this.socket.emit("playCard");
                console.log("it worked!");
            },
            voteBlue() {
                this.socket.emit("vote", "blue");
            },
            voteRed() {
                this.socket.emit("vote", "red");
            },
            sendID() {
                this.socket.emit("sendID");
            },
            sendHero() {
                console.log("Hero Sent!");
            },
            testFunction() {
                this.socket.emit("vote", "blue");
            },
            scrollUp() {
                window.scrollTo(0,0);
                
            },
            scrollDown() {
                window.scrollTo(0,document.body.scrollHeight);
            }
        },
    }
</script>

<style scoped>

@import url('https://fonts.googleapis.com/css2?family=Risque&display=swap');

.gameBoard {
    font-family: 'Risque', cursive;
    background: url("../assets/lightning.jpg") rgba(0, 0, 0, 0.4);
    background-size: cover;
    background-blend-mode: multiply;
    margin: auto;
    position: absolute;
    top:0;
    bottom: 0;
    left:0;
    right:0;
    

}

@media (max-width: 992px) {
    .gameBoard {
        position: relative;
    }
}

</style>