<template>
    <div class="hand">
        <Card v-bind:text=hand[0] v-on:click.native="pickHero(0)" />
        <Card v-bind:text=hand[1] v-on:click.native="pickHero(1)" />
        <Card v-bind:text=hand[2] v-on:click.native="pickHero(2)" />
        <Card v-bind:text=hand[3] attr v-on:click.native="pickAttr(3)" />
        <Card v-bind:text=hand[4] attr v-on:click.native="pickAttr(4)" />
        <Card v-bind:text=hand[5] attr v-on:click.native="pickAttr(5)" />
        <ChooseHero v-if='status=="chooseHero"' v-bind:hero="this.hero" v-bind:attr="this.attr" />

    </div>  
</template>

<script>
    import Card from './Card.vue';
    import ChooseHero from './ChooseHero';


    export default {
        name: 'Hand',
        components: {
            Card,
            ChooseHero
        },
        props: {
            hand: {
                type: Array,
                Default: []
            },
            status: {
                type: String,
                default: ""
            }
        },
        methods: {
            pickHero(position) {
                this.hero=this.hand[position];
            },
            pickAttr(position) {
                this.attr=this.hand[position];
            }
        },
        data() {
            return {
                hero: "",
                attr: "",
            }
        }
    }
</script>

