.hand {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    width: 1000px;
    margin-top: 200px;
    background-color: #666;
    border-radius: 15px 15px 0 0;
    padding-top: 50px;
    max-width: 100%;
